@import url("/fss/commons/css/common.css");
@import url("/fss/fss/css/default.css");
@import "base/variables"; //환경 설정
@import "../../fss/scss/breakpoint/breakpoint"; //반응형 플러그인
#w {
	position: relative;
	// border: 1px solid #000;
	// max-width: 1920px;
	margin: 0 auto;
	.header {
		position: fixed;
		left: 0;
		top: 0;
		width: 100%;
		height: 150px;
		background: #fff url(/fss/img/nav-o.png) center 0 no-repeat;
		transition: background-color 0.5s ease, height 0.3s;
		border-bottom: 1px solid #e9e9e9;
		z-index: 9999;
		.gnb-stiky & {
			background-image: url(/fss/img/nav.png);
			background-color: #2c428e;
			height: 70px;
			border-bottom-width: 0;
		}
	}
}
img {
	vertical-align: top;
}
.nav {
	position: absolute;
	position: fixed;
	left: 0;  
	left: 44px;
	top: 50%;
	a {
		display: block;
		font-size: 12px;
		font-family: "Noto Sans CJK KR"; 
		color: rgb(102, 102, 102);
		line-height: 17px;
		i {
			width: 17px;
			height: 17px;
			margin-right: 10px;
			display: inline-block;
			vertical-align: top;
			background: url(/fss/img/c.png) center center no-repeat;
		}
		&.active,
		&:hover {
			color: #000;
			i {
				background: url(/fss/img/c-l.png) center center no-repeat;
			}
		}
		& + a {
			padding-top: 20px;
			position: relative;
			&::before {
				content: "";
				background-color: #008fd4;
				width: 1px;
				position: absolute;
				height: 30px;
				// top: -30px;
				top: -5px;
				left: 8px;
			}
		}
	}
	.mouse{
		position: absolute;
		top: 40px;
		left: 120px;
		width: 18px;
		height: 44px;
		background: url(/fss/img/mouse.png) 0 0 no-repeat;
	}
	.whell{
		position: absolute;
		left: 8px;
		top: 17px;
		width: 2px;
		height: 4px;
		background: url(/fss/img/whell.png) 0 0 no-repeat;
	}
}
.section {
	text-align: center;
	height: 100vh;
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: center;
}
.section1{
	background-color: #f6f6f6;
}
.section2 {
	position: relative;
	overflow: hidden;
	&::before {
		content: "";
		// 420/1080=0.38888888888888888889*100=38.888888888888888889
		// 1080/2=540-420=120
		padding-top: 38.888888888888888889%;
		background-color: #dde6f0;
		display: block;
		width: 100%;
		// width: 100vw;
		position: absolute;
		top: calc(50% + 120px);
		// bottom: 0;
		left: 0;
		z-index: -1;
	}
}
.section3 {
	background-color: #f6f6f6;
}
.section4 {
	background-color: #e5ecf3;
}
.footer {
	text-align: center;
	background: url(/fss/img/footer-x-bg-pt.png) 0 0 repeat-x;
}
aside {
	position: fixed;
	right: -548px;
	top: 50%;
	transform: translateY(-50%);
	cursor: pointer;
	transition: all 0.5s ease-in-out;
	transition: all 0.5s cubic-bezier(0.65, 0.05, 0.36, 1);
	&.active {
		right: 0 !important;
	}
}
.animated {
	-webkit-animation-duration: 1.5s;
	animation-duration: 1.5s;
	-webkit-animation-fill-mode: both;
	animation-fill-mode: both;
	-webkit-animation-timing-function: linear;
	animation-timing-function: linear;
	animation-iteration-count: infinite;
	-webkit-animation-iteration-count: infinite;
  } 
  @keyframes bounce { 
	// 0%, 20%, 40%, 60%, 80%, 100% {transform: translateY(0);}
	0%,  100% {transform: translateY(0);}
	50% {transform: translateY(-4px);}
  } 
  .bounce { 
	-webkit-animation-name: bounce;
	animation-name: bounce;
  }